body{background-color:#dadada}body .admin{width:100%;min-height:90vh;padding-top:20px;text-align:center;font-family:"Aqum",serif;display:flex;flex-direction:column;justify-content:center;align-items:center}body .admin .button_function{display:block;width:200px;margin:20px auto auto}body .admin h1,body .admin h2{font-weight:bold;font-size:22px}body .admin form{width:50%;margin:20px 0}body .admin form input,body .admin form button{display:block;width:350px;height:30px;margin:20px auto;border-radius:20px;border:2px solid #000;color:#000;background-color:#c8c8c8;padding-left:10px;font-size:18px}body .admin table{margin:20px auto auto;text-align:center;width:90%;border:1px solid gray}body .admin table caption{color:red}body .admin table th{border:1px solid gray}body .admin table td{border:1px solid gray}body .admin .table_sort table{border-collapse:collapse}body .admin .table_sort th{color:#ffebcd;background:#1e8c34;cursor:pointer}body .admin .table_sort td,body .admin .table_sort th{width:150px;height:40px;text-align:center;border:2px solid #846868}body .admin .table_sort tbody tr:nth-child(even){background:#e3e3e3}body .admin th.sorted[data-order="1"],body .admin th.sorted[data-order="-1"]{position:relative}body .admin th.sorted[data-order="1"]::after,body .admin th.sorted[data-order="-1"]::after{right:8px;position:absolute}body .admin th.sorted[data-order="-1"]::after{content:"▼"}body .admin th.sorted[data-order="1"]::after{content:"▲"}